@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*,
*::after,
*::before {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}
.fWeight-1000 {
  font-weight: 1000;
}
body {
  background-color: rgb(251, 251, 251);
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  min-width: 300px;
}
#root {
  font-size: 1em;
}

/* Parent div of every page */
.container {
  width: 95%;
  margin: 2em auto;
  background-color: white;
  box-shadow: 0px 0px 2px black;
}

/* CSS for every btn used in this assignment */
.btn {
  color: blue;
  border: 0.1em solid blue;
  background-color: transparent;
  transition: ease-in-out 0.2s;
}
.btn:hover {
  color: white;
  background-color: blue;
  cursor: pointer;
}

/*  dash :- dashboard */

.dash-heading {
  margin: 0.6em 0 0.5em 0;
  padding: 3em 0.6em 0 0.6em;
}
.dash-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  border-bottom: 0.1em solid rgb(121, 120, 120);
}
.dash-container * {
  margin: 0.8em;
}
/*   botName :- Name of the product   */
.botName {   
  width: 30%;
}
.value {
  width: 15%;
}
.cgmr-value {
  color: rgb(0, 184, 0);
}
.dash-buttons {
  display: flex;
  flex-direction: column;
}
.dash-buttons * {
  padding: 0.3em;
  border-radius: 5px;
  text-align: center;
}
.link-seemore {
  text-decoration: none;
  border: 0.1em solid rgb(16, 212, 196);
  color: rgb(16, 212, 196);
  transition: ease-in-out 0.2s;
}
.link-seemore:hover {
  color: white;
  background-color: rgb(16, 212, 196);
}

@media only screen and (max-width: 515px) {
  .dash-buttons {
    width: 100%;
  }
}

.navbar {
  box-shadow: 1px 1px 3px black;
  background-color: white;
}
.menu {
  margin: 0% 3em;
  line-height: 3.3em;
}
.menu * {
  color: black;
  padding: 0.9em;
  text-decoration: none;
}
.menu *:hover,
a.dashboard,
a.cart {
  color: rgb(90, 90, 90);
  border-bottom: 0.2em solid black;
  border-radius: 5px;
}

.bot-des {
  width: 60%;
}
.details-btn {
  margin: 0.8em 0em;
}

.moderate-icon-container {
  display: inline;
  padding: 0.6em;
  margin: 0.8em 0 0.8em 0.8em;
  color: white;
  background-color: rgb(4, 156, 144);
  border-radius: 5px 0px 0px 5px;
}
.details-btn * {
  font-size: 0.8em;
}
.details-buy {
  padding: 0.5em 0.4em;
  border-radius: 0px 5px 5px 0px;
}
.moderate-icon {
  margin-left: 0%;
  padding: 0.4em;
  color: rgb(4, 156, 144);
  border: 0.2em solid rgb(4, 156, 144);
}

@media only screen and (max-width: 710px) {
  .bot-des {
    width: 50%;
  }
}

.del-btn {
  padding: 0.3em;
  border-radius: 5px;
  text-align: center;
  color: red;
  border: 0.1em solid red;
}
.del-btn:hover {
  background-color: red;
  color: white;
}

@media only screen and (max-width: 365px) {
  .del-btn {
    width: 80%;
    margin: 0em auto 1em auto;
  }
}

